.cart-container {
  background: #f6f9ff;
  position: relative;
  // padding: 0 150px 0 150px;

  .title-header {
    padding-top: 30px;
  }

  .title-item {
    padding: 32px 0 16px 0;

    .title {
      font-size: 36px;
    }
  }

  .content-item {
    width: 100% !important;
    margin-bottom: 0 !important;
    .empty-cart {
      font-family: 'Google Sans', sans-serif;
      font-size: 16px;
    }
    .error-message-promotion-code {
      font-family: 'Google Sans', sans-serif;
      font-size: 12px;
      color: #ff4355;
      margin-top: 10px;
      margin-bottom: 10px;
    }
    // padding: 0px 1em 0px 2em;
  }
  .right-section {
    align-items: center;
    justify-content: center;
  }
}
